<template>
    <a :href="href" @click.prevent="scroll"> <slot></slot> </a>
</template>
<script>
export default{
    props:['href'],

    methods:{
      scroll(){
        //document.querySelector(this.href).scrollIntoView({behavior: 'smooth'});
        const offset = 100;

                // Calculate the offset from the top of the element
                const elementPosition = document.querySelector(this.href).getBoundingClientRect().top + window.scrollY;

                console.log(document.querySelector(this.href).getBoundingClientRect().top + "faisj");
                if(!document.querySelector(this.href)){
                    window.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });
                return;
                }
                
                // Using smooth scroll behavior and offset
                window.scrollTo({
                    top: elementPosition - offset,
                    behavior: 'smooth'
                });

    }
    }
  }
</script>